html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: "Open Sans",Raleway,Roboto,-apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", "Helvetica Neue", Arial, sans-serif; }
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }
body {
  line-height: 1.5;
  color: #666666; }
h1,h2,h3,h4,h5,h6 {
  line-height: 1.2;
  font-weight: bold;
  font-family: Raleway;
  color: #212121; }
h2 {
  font-size: 32px; }
h3 {
  font-size: 24px; }
ol, ul {
  list-style: none; }
blockquote, q {
  quotes: none; }
blockquote {
  &:before, &:after {
    content: '';
    content: none; } }
q {
  &:before, &:after {
    content: '';
    content: none; } }
table {
  border-collapse: collapse;
  border-spacing: 0; }
a {
  text-decoration: none;
  &:hover {
    text-decoration: none; } }

*, *::before, *::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

select {
  appearance: none;
  background: url(../img/dropdown.svg) no-repeat transparent 65px center;
  width: 85px; }

.container {
  max-width: 1110px;
  margin: 0 auto;
  @include md {
    max-width: 900px; }
  @include pad {
    max-width: 690px; }
  @include phone {
    max-width: 390px; }
  @include small {
    max-width: 350px; }
  @include xxs {
    max-width: 300px; } }
