.memberNavbar {
  position: relative;
  .memberSelect {
    display: flex;
    align-items: center; }
  .photo {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
    & img {
      width: 100%; } }
  .name {
    margin-right: 10px;
    color: $darkenColor;
    font-weight: 500; }
  .arrow {
    font-size: 16px;
    cursor: pointer;
    width: 20px;
    text-align: right;
    & i {
      vertical-align: top; } }
  .dropDown {
    position: absolute;
    right: 0;
    top: 65px;
    border-radius: 4px;
    overflow: hidden;
    display: none;
    & li {
      border-bottom: 1px solid #fff; }
    & a {
      display: block;
      padding: 10px 20px;
      background-color: $lightPrimary;
      color: $primaryColor;
      transition: 0.5s;
      &:hover {
        background-color: darken($lightPrimary,5%); } } } }
