.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 27px 0px;
  font-size: 14px;
  @include phone {
    padding: 24px 0px; }
  .footerText {
    display: flex;
    align-items: center;
    @include pad {
      width: 100%;
      justify-content: space-between; }
    @include phone {
      flex-wrap: wrap;
      justify-content: center; } }
  .copyRight {
    margin-right: 64px;
    @include phone {
      margin-right: 0;
      order: 2; } }
  .footerMenu {
    display: flex;
    align-items: center;
    @include phone {
      order: 1; }
    @include phone {
      margin-bottom: 15px; }
    & li {
      margin-right: 24px;
      &:last-child {
        @include pad {
          margin-right: 0; } } }
    & a {
      color: $secondaryColor;
      &:hover {
        color: $primaryColor; } } }
  .footerOption {
    display: flex;
    align-items: center;
    @include pad {
      display: none; }
    & li {
      display: flex;
      align-items: center;
      margin-right: 24px;
      &:last-child {
        margin-right: 0; } }
    .icon {
      margin-right: 10px;
      color: $darkenColor; }
    .select {
      font-size: 14px;
      border: none;
      cursor: pointer;
      padding-right: 10px;
      font-family: "Open Sans";
      font-weight: 500;
      color: $secondaryColor;
      &:focus {
        outline: 0; } }
    .coinType {
      width: 60px;
      background-position: 40px center; } } }
