 }  // height: 100%
body,html {
  height: 100vh; }
.login {
  min-height: 100%;
  display: flex;
  position: relative;
  .bgImg {
    position: fixed;
    top: 0;
    bottom: 0;
    background-image: url(../img/login.jpg);
    background-size: cover;
    background-position: center 60%;
    width: 60%;
    z-index: -2;
    @include pad {
      width: 100%;
      background-position: center 0%; } }
  .loginContent {
    display: flex;
    flex: 1;
    position: relative;
    margin-top: 180px;
    @include pad {
      margin-top: 140px; }
    @include phone {
      margin-top: 100px; }
    @include xxs {
      margin-top: 65px; } }
  .loginText {
    width: 66%;
    @include pad {
      display: none; }
    .title {
      font-size: 48px;
      font-weight: 800;
      margin-bottom: 15px; }

    .text {
      font-size: 14px; } }
  .loginForm {
    width: 34%;
    @include pad {
      width: 50%;
      margin: 0 auto; }
    @include phone {
      width: 90%; }
    .form {
      @include phone {
        background-color: rgba(black,0.10);
        padding: 15px;
        color: #fff;
        border-radius: 4px; } }
    .title {
      margin-bottom: 16px; }
    & label,& input {
      display: block; }
    & label {
      margin-bottom: 5px; }
    & input {
      width: 100%;
      border-radius: 4px;
      border: 1px solid $lightColor;
      padding: 16px 10px;
      font-size: 16px;
      margin-bottom: 16px;
      &:focus {
        outline: 0;
        background-color: $lightPrimary;
        border: 1px solid $lightPrimary; } }
    .text {
      font-size: 12px;
      margin-bottom: 16px; }
    .active {
      color: $primaryColor; }
    .checkbox {
      display: block;
      position: relative;
      padding-left: 24px;
      margin-bottom: 32px;
      cursor: pointer;
      user-select: none; }
    .checkbox {
      & input {
        position: absolute;
        opacity: 0;
        height: 0;
        width: 0;
        cursor: pointer; } }
    .checkmark {
      position: absolute;
      top: 3px;
      left: 0;
      height: 18px;
      width: 18px;
      background-color: $lightPrimary;
      border: 1px solid $lightPrimary;
      border-radius: 4px;
      @include phone {
        background-color: #fff; } }
    .checkbox input:checked ~ .checkmark {
      background-color: $primaryColor; }
    .checkbox .checkmark::before {
      left: 5px;
      top: 0px;
      width: 7px;
      height: 14px;
      border: solid #fff;
      border-width: 0px 3px 3px 0;
      transform: rotate(45deg); }
    .checkmark::before {
      content: "";
      position: absolute;
      display: none; }
    .checkbox input:checked ~ .checkmark::before {
      display: block; }
    .loginBtn {
      text-align: center;
      display: block;
      padding: 17px;
      background-color: $primaryColor;
      color: #fff;
      font-weight: bold;
      border-radius: 4px;
      margin-bottom: 8px;
      transition: 0.5s;
      &:hover {
        background-color: darken($primaryColor,5%); } } }
  .signUpText {
    font-size: 14px;
    text-align: center;
    @include phone {
      color: #fff; }
    & a {
      font-weight: bold;
      color: $primaryColor;
      @include phone {
        color: #fff; }
      &:hover {
        color: darken($primaryColor,5%);
        @include phone {
          color: #fff; } } } }
  .marker {
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 5%;
    left: 11.5%;
    background-color: $darkenColor;
    padding: 8px 16px;
    border-radius: 4px;
    color: #fff;
    font-size: 14px;
    @include padPlus {
      left: 6%; }
    @include pad {
      left: 50%;
      transform: translateX(-50%); }
    @include xxs {
      display: none; }
    & i {
      margin-right: 10px; } } }
