@mixin md {
  @media (max-width: 1160px) {
    @content; } }

@mixin padPlus {
  @media (max-width: 1025px) {
    @content; } }

@mixin pad {
  @media (max-width: 992px) {
    @content; } }

@mixin phone {
  @media (max-width: 767px) {
    @content; } }

@mixin xs {
  @media (max-width: 415px) {
    @content; } }

@mixin small {
  @media (max-width: 376px) {
    @content; } }

@mixin xxs {
  @media (max-width: 321px) {
    @content; } }
