.received {
  margin-top: 24px;
  max-width: 740px;
  min-height: calc(100vh - 181px);
  border-bottom: 1px solid #eee;
  @include pad {
    max-width: 690px; }
  @include phone {
    max-width: 390px; }
  @include small {
    max-width: 350px; }
  @include xxs {
    max-width: 300px; }
  .title {
    padding-bottom: 32px;
    border-bottom: 1px solid #eee;
    @include phone {
      font-size: 24px;
      padding-bottom: 24px; } }
  .receivedContent {
    margin-bottom: 24px;
    & li {
      padding: 24px 0px;
      border-bottom: 1px solid #eee; }
    .content {
      display: flex;
      margin-bottom: 16px;
      &:last-child {
        margin-bottom: 0; }
      .name {
        font-weight: bold;
        color: $darkenColor;
        width: 30%;
        @include phone {
          font-size: 14px;
          width: 25%; } }
      .text {
        width: 70%;
        @include phone {
          font-size: 14px;
          width: 75%; } }
      .confirmation {
        color: $primaryColor;
        margin-left: 15px;
        font-size: 12px;
        @include phone {
          display: block;
          margin-left: 0; }
        &:hover {
          color: darken($primaryColor,10%); } } } }
  .btns {
    display: flex;
    justify-content: flex-end;
    @include phone {
      flex-wrap: wrap; }
    .edit {
      font-size: 16px;
      cursor: pointer;
      border-radius: 4px;
      padding: 17px 32px;
      margin-right: 8px;
      background-color: #fff;
      border: 1px solid $primaryColor;
      color: $primaryColor;
      transition: 0.5s;
      font-weight: bold;
      @include phone {
        width: 100%;
        margin-bottom: 8px;
        margin-right: 0;
        padding: 19px 32px; }
      &:hover {
        background-color: $primaryColor;
        color: #fff; } }
    .home {
      border-radius: 4px;
      padding: 17px 32px;
      background-color: $primaryColor;
      color: #fff;
      transition: 0.5s;
      display: block;
      font-weight: bold;
      @include phone {
        width: 100%;
        text-align: center; }
      &:hover {
        background-color: darken($primaryColor,5%); } } } }
