.toTop {
  position: fixed;
  right: -20px;
  bottom: 10%;
  background-color: $lightPrimary;
  border-radius: 15px 0px 0px 15px;
  box-shadow: 2px 2px 5px $primaryColor;
  opacity: 0;
  transition: 0.5s;
  z-index: 999;
  &:hover {
    background-color: $primaryColor;
    & a,.arrowUp {
      color: #fff; } }
  & a {
    display: block;
    color: $primaryColor;
    font-weight: bold;
    padding: 10px 30px 10px 10px;
    transition: 0.5s; }
  &.active {
    opacity: 1; }
  .arrowUp {
    padding-left: 8px;
    display: block;
    color: $primaryColor;
    margin-bottom: -4px;
    transition: 0.5s; } }
.headerSwiper {
  position: relative;
  .swiper {
    height: 350px; }
  .swiper-slide {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    position: relative;
    & p {
      font-size: 48px;
      font-family: Raleway;
      font-weight: 800;
      z-index: 99;
      @include padPlus {
        width: 400px; }
      @include phone {
        width: 293px;
        font-size: 32px; } }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: black;
      opacity: 0.2; } }
  .swiper-button-prev {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 250px;
    @include padPlus {
      left: 160px; }
    @include pad {
      left: 35px; }
    @include phone {
      left: 5px; }
    &::after {
      font-size: 30px;
      color: #fff;
      font-weight: bold; } }
  .swiper-button-next {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 250px;
    @include padPlus {
      right: 160px; }
    @include pad {
      right: 35px; }
    @include phone {
      right: 4px; }
    &::after {
      font-size: 30px;
      color: #fff;
      font-weight: bold; } }
  .bgCover {
    background-size: cover;
    background-position: center center;
    cursor: pointer; } }

.headerPic {
  background-image: url(../img/banner.jpg); }
.headerPic1 {
  background-image: url(../img/banner1.jpg); }
.headerPic2 {
  background-image: url(../img/banner2.jpg); }

.searchAttractions {
  width: 920px;
  position: absolute;
  margin-top: -50px;
  z-index: 5;
  padding: 24px;
  border-radius: 8px;
  background-color: $lightPrimary;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  justify-content: space-between;
  @include pad {
    flex-wrap: wrap;
    max-width: 570px;
    margin-top: -73px; }
  @include phone {
    max-width: 395px; }
  @include xs {
    max-width: 345px; }
  @include xxs {
    max-width: 285px; }
  & li {
    width: calc(100% / 3 );
    margin-right: 16px;
    background-color: #fff;
    border-radius: 4px;
    padding: 16px;
    cursor: pointer;
    @include pad {
      width: 100%;
      margin-right: 0;
      margin-bottom: 16px; }
    &:last-child {
      margin-right: 0;
      @include pad {
        margin-bottom: 0; } } }
  .local,.date,.personNumber {
    display: flex;
    align-items: center;
    position: relative; }
  .date {
    .inputs {
      display: flex;
      align-items: center;
      & input {
        width: 85px;
        font-size: 16px;
        color: $secondaryColor;
        cursor: pointer;
        border: none;
        margin-right: 5px;
        &:focus {
          outline: 0; }
        &:last-child {
          margin-right: 0;
          margin-left: 10px; } } } }
  .icon {
    margin-right: 16px;
    & i {
      color: $darkenColor; } }
  .searchBtn {
    width: 20%;
    padding: 0;
    @include pad {
      width: 100%; }
    & a {
      border-radius: 4px;
      display: block;
      padding: 16px;
      color: #fff;
      text-align: center;
      background-color: $primaryColor;
      transition: 0.5s;
      font-weight: bold;
      &:hover {
        background-color: darken($primaryColor,5%); } } }
  .localList {
    display: none;
    position: absolute;
    user-select: none;
    width: 100px;
    top: 68px;
    left: 0;
    width: 100%;
    background-color: #fff;
    z-index: 10;
    & li {
      display: flex;
      width: 100%;
      padding: 5px 16px;
      transition: 0.5s;
      @include pad {
        padding: 10px 16px;
        margin-bottom: 0; }
      &:hover {
        background-color: $lightPrimary; }
      .nation {
        color: $darkenColor; }
      .city {
        font-size: 12px; } }
    &.active {
      display: block; }
    .text {
      width: 100%; } }
  .number {
    position: absolute;
    right: 153px;
    width: 220px;
    top: 90px;
    padding: 5px 16px;
    display: none;
    user-select: none;
    background-color: #fff;
    @include pad {
      right: 327px;
      top: 239px; }
    @include phone {
      right: 22px;
      width: 300px; }
    &.active {
      display: block; }
    .adult,.child,.room {
      display: flex;
      align-items: center;
      width: 188px;
      @include phone {
        display: flex;
        justify-content: space-between;
        width: auto; } }
    .text {
      width: 100px;
      color: $darkenColor;
      cursor: default; }
    .input {
      width: 60px;
      text-align: center;
      font-size: 18px;
      border: none;
      color: $secondaryColor;
      &:focus {
        outline: 0; } }
    .minus,.add {
      font-size: 28px;
      font-weight: bold;
      cursor: pointer;
      width: 30px;
      text-align: center;
      user-select: none; } } }

input.input[type=number]::-webkit-outer-spin-button,
input.input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }
input.input[type=number] {
  -moz-appearance: textfield; }

.pika-single {
  @include phone {
    width: 260px; } }

.pika-single.is-bound.left-aligned.bottom-aligned {
  margin-top: 30px;
  @include phone {
    margin-left: -80px !important; } }
.pika-single.is-bound.bottom-aligned.right-aligned {
  margin-top: 30px;
  @include pad {
    margin-left: 270px; } }

.choicesContent {
  user-select: none; }
.choices {
  margin-top: 150px;
  margin-bottom: 72px;
  @include pad {
    margin-top: 290px; }
  @include small {
    margin-bottom: 80px; }
  .title {
    margin-bottom: 24px;
    @include phone {
      font-size: 24px; } }
  .tabTags {
    display: flex;
    margin-bottom: 28px; }
  .tab {
    cursor: pointer;
    padding: 0 24px;
    text-align: center;
    color: $secondaryColor;
    font-weight: bold;
    font-family: Raleway;
    font-size: 24px;
    border-bottom: 2px solid lighten($secondaryColor,50%);
    transition: 0.5s;
    @include phone {
      font-size: 20px;
      width: calc(100% / 3);
      padding: 0; }
    @include xxs {
      font-size: 18px; }

    &.active {
      border-bottom: 2px solid $primaryColor;
      color: $primaryColor;
      font-weight: bold; }
    &:hover {
      border-bottom: 2px solid $primaryColor;
      color: $primaryColor; } }
  .tabInner {
    display: none; }
  .tabInner.active {
    display: block;
    position: relative; }
  .tabInner {
    position: relative; }
  .img {
    height: 175px;
    width: 100%;
    object-fit: cover;
    object-position: center center;
    border-radius: 8px;
    margin-bottom: 5px; }
  .subTitle {
    color: $darkenColor;
    font-weight: bold; }
  .star {
    margin-bottom: 5px; }
  .starIcon {
    & i {
      font-size: 14px;
      color: $darkenColor; } }
  .score {
    font-size: 14px;
    margin-left: 6px; }
  .reviews {
    font-size: 12px;
    color: $lightColor; }
  .price {
    font-size: 14px; }
  .originPrice {
    font-size: 12px;
    text-decoration: line-through;
    margin-left: 10px;
    color: $lightColor; }
  .swiper-slide {
    & a:active,a:visited {
      color: $secondaryColor; } }
  .swiper-button-prev.prev1,.swiper-button-prev.prev2,.swiper-button-prev.prev3 {
    background-color: $lightPrimary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 100px;
    left: -20px;
    @include phone {
      top: 280px;
      left: 290px; }
    @include small {
      top: 325px;
      left: 250px; }
    @include xxs {
      top: 290px;
      left: 205px; }
    &::after {
      font-size: 14px;
      color: #000;
      font-weight: bold; } }
  .swiper-button-next.next1,.swiper-button-next.next2,.swiper-button-next.next3 {
    position: absolute;
    top: 100px;
    right: -20px;
    background-color: $lightPrimary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    @include phone {
      top: 280px;
      right: 0px; }
    @include small {
      top: 325px; }
    @include xxs {
      top: 290px; }
    &::after {
      font-size: 14px;
      color: #000;
      font-weight: bold; } }
  .swiper-button-prev.swiper-button-disabled {
    opacity: 0;
    @include phone {
      opacity: 0.2; } }
  .swiper-button-next.swiper-button-disabled {
    opacity: 0;
    @include phone {
      opacity: 0.2; } } }

.popularLocation {
  margin-bottom: 48px;
  .subTitle {
    margin-bottom: 24px;
    @include phone {
      font-size: 24px; } }
  .location {
    display: flex;
    flex-wrap: wrap;
    @include pad {
      flex-wrap: wrap; }
    & li {
      width: calc(100% / 3 - 20px);
      padding: 16px 25px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-right: 30px;
      margin-bottom: 24px;
      position: relative;
      border-radius: 8px;
      overflow: hidden;
      cursor: pointer;
      @include pad {
        width: calc(100% / 2 - 15px); }
      @include phone {
        width: 100%;
        margin-right: 0; }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        transition: 0.5s;
        background: linear-gradient(to right, rgb(20, 20, 20) 30%, rgb(104, 104, 104) 90%);
        opacity: 0.6; }
      &:hover {
        &::before {
          opacity: 0; }
        .country {
          color: #fff; } }
      &:nth-child(3n+3) {
        margin-right: 0;
        @include pad {
          margin-right: 30px; }
        @include phone {
          margin-right: 0; } }
      &:nth-child(2n+2) {
        @include pad {
          margin-right: 0; } } }
    .name {
      z-index: 5; }
    .country {
      font-size: 12px;
      color: #FFFFFF98; }
    .city {
      color: #fff; }
    .bgCover {
      background-size: cover; }
    .icon {
      color: #fff;
      z-index: 5; } } }
.inspiration {
  margin-bottom: 72px;
  @include phone {
    margin-bottom: 90px; }
  .subTitle {
    @include phone {
      font-size: 24px; } }
  .header {
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .seeMoreBtn {
    font-size: 14px;
    color: $primaryColor;
    border: 1px solid $primaryColor;
    font-weight: bold;
    padding: 8px 16px;
    display: block;
    border-radius: 4px;
    transition: 0.5s;
    &:hover {
