  & li {
    width: calc(100% / 3);
    margin-right: 30px;
    border-radius: 8px;
    overflow: hidden;
    &:nth-child(3n+3) {
      margin-right: 0; } }
  .img {
    margin-bottom: 24px;
    border-radius: 8px;
