    border-radius: 50%;
    width: 40px;
    height: 40px;
    position: absolute;
    left: 285px;
    top: 360px;
    @include small {
      left: 245px; }
    @include xxs {
      left: 200px;
      top: 315px; }
    &::after {
      font-size: 14px;
      color: #000;
      font-weight: bold; } }
  .swiper-button-next.next4 {
    position: absolute;
    top: 360px;
    right: 3px;
    background-color: $lightPrimary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    @include xxs {
