.subscribe {
  background-color: $lightPrimary;
  padding: 92px 0px;
  @include phone {
    padding: 24px 0px; }
  .subscribeCotnent {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .text {
    font-size: 48px;
    color: $darkenColor;
    font-family: Raleway;
    font-weight: 800;
    margin-bottom: 24px;
    @include phone {
      font-size: 24px; }
    @include xxs {
      font-size: 20px; } }
  .subscribeBtn {
    display: flex;
    align-items: center;
    text-align: center;
    .input {
      background-color: #fff;
      border: none;
      padding: 16px 21px;
      width: 400px;
      border-radius: 4px 0px 0px 4px;
      font-size: 16px;
      height: 100%;
      border: 1px solid transparent;
      font-family: 'Open Sans';
      @include phone {
        width: 215px; }
      @include small {
        width: 185px;
        padding: 16px; }
      @include xxs {
        width: 165px;
        padding: 16px 10px; }
      &:focus {
        outline: 0;
        border: 1px solid $primaryColor; } }
    .sendBtn {
      background-color: $primaryColor;
      border: none;
      font-family: 'Open Sans';
      padding: 17px 32px;
      color: #fff;
      font-size: 16px;
      border-radius: 0px 4px 4px 0px;
      height: 100%;
      font-weight: bold;
      cursor: pointer;
      transition: 0.5s;
      @include phone {
        padding: 17px 32px; }
      @include xxs {
        padding: 17px 15px; }
      &:hover {
        background-color: darken($primaryColor,5%); } } } }

.pika-lendar {
  .is-today .pika-button {
    color: $primaryColor; }
  .is-today.is-selected .pika-button {
    color: #fff; }
  .is-selected .pika-button {
    background: $primaryColor;
    box-shadow: none; }
  .pika-button:hover {
    background-color: darken($lightPrimary,10%); } }
