.hotelImgs {
  margin-bottom: 24px;
  @include phone {
    display: none; }
  .imgContent {
    display: flex;
    height: 434px;
    @include pad {
      height: 300px; } }
  .main,.other,.pic {
    overflow: hidden;
    & img {
      border-radius: 4px; } }
  .other {
    width: 44%; }
  .pic {
    width: 50%; }
  .main {
    width: 60%;
    margin-right: 8px;
    & img {
      height: 100%;
      object-fit: cover; } }
  .top,.bottom {
    height: 50%;
    & img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  .top {
    margin-bottom: 8px; }
  .bottom {
    display: flex;
    .pic,.pic1 {
      width: 50%; }
    .pic {
      margin-right: 8px;
      border-radius: 4px;
      overflow: hidden;
      height: 96%;
      @include pad {
        height: 94%; }
      & img {
        border-radius: 4px; } }
    .pic1 {
      @include pad {
        height: 97%; } }
    .pic2 {
      height: 70%;
      margin-bottom: 8px; } }
  .moreImg {
    text-align: center;
    background-color: $darkenColor;
    color: #fff;
    height: 22%;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    cursor: pointer;
    transition: 0.5s;
    @include pad {
      font-size: 12px; }
    &:hover {
      background-color: darken($primaryColor,5%); } } }
.describe {
  margin-bottom: 72px;
  .content {
    width: 57%;
    @include phone {
      width: 100%; } }
  .describeHead {
    display: flex;
    align-items: center;
    @include phone {
      flex-wrap: wrap;
      margin-bottom: 10px; }
    .title {
      margin-right: 16px; } }
  .star {
    @include phone {
      width: 100%; }
    & i {
      font-size: 14px;
      letter-spacing: 1px;
      color: $darkenColor; }
    & span {
      margin-left: 10px;
      font-size: 14px; } }
  .address {
    color: $primaryColor;
    font-size: 14px;
    margin-bottom: 24px;
    @include phone {
      font-size: 12px;
      margin-bottom: 16px; } }
  .text {
    font-size: 14px;
    text-align: justify;
    text-justify: inter-ideograph; } }
.selectRoom {
  margin-bottom: 72px;
  .title {
    margin-bottom: 24px; }
  .selectBar {
    display: flex;
    align-items: center;
    margin-bottom: 48px;
    @include phone {
      display: none; } }
  .info {
    display: flex;
    align-items: center;
    & li {
      margin-right: 8px;
      border-radius: 4px;
      background-color: $lightPrimary;
      padding: 5px 16px;
      font-size: 14px; } }
  .edit {
    color: $primaryColor;
    font-weight: bold;
    cursor: pointer;
    margin-left: 10px;
    &:hover {
      color: darken($primaryColor,5%); } }
  .responsive {
    overflow-x: auto;
    @include phone {
      display: none; }
    &::-webkit-scrollbar {
      width: 10px;
      height: 10px;
      background-color: #F7F7F7; }
    &::-webkit-scrollbar-thumb {
      background-color: #E6E6E6;
      border-radius: 10px; } }
  .table {
    width: 100%;
    @include padPlus;
    & th {
      color: $darkenColor;
      font-weight: bold;
      padding-bottom: 10px; }
    & tr {
      border-bottom: 1px solid #eee; }

    & td {
      padding: 24px 0px;
      vertical-align: middle; }
    & tbody {
      & tr {
        transition: 0.5s; }
      & tr:hover {
        background-color: $lightPrimary; } }
    .roomType {
      @include pad {
        width: 350px; } }
    .detail {
      display: flex;
      .serve {
        width: 55%;
        @include pad {
          width: 70%; } }
      .img {
        width: 45%;
        @include pad {
          width: 50%; }
        & img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center center;
          border-radius: 4px 0px 0px 4px; } } }
    .serve {
      padding: 8px 16px;
      @include pad {
        padding-right: 0; }
      .name {
        font-weight: bold;
        color: $darkenColor; }
      .bedNum {
        font-size: 12px;
        margin-bottom: 16px; } }
    .serveContent {
      display: flex;
      flex-wrap: wrap;
      & li {
        width: calc(100% / 2);
        margin-bottom: 8px;
        @include pad {
          width: calc(100% / 6); }
        &:last-child {
          width: 100%; }
        & i {
          width: 20px;
          margin-right: 10px;
          text-align: center; }
        & span {
          font-size: 14px;
          @include pad {
            display: none; } } } }
    .sleeps {
      & i {
        letter-spacing: 3px;
        color: $darkenColor;
        font-size: 20px; } }
    .origin {
      text-decoration: line-through;
      font-size: 14px; }
    .price {
      color: $darkenColor;
      font-weight: bold;
      & span {
        margin-left: 5px; }
      & span.active {
        color: $primaryColor; } }
    .night {
      font-size: 12px; }
    .rooms {
      display: flex;
      justify-content: center;
      align-items: center;
      .min,.add {
        font-size: 28px;
        font-weight: bold;
        color: $darkenColor;
        cursor: pointer;
        width: 25px;
        text-align: center;
        user-select: none; }
      .input {
        max-width: 40px;
        text-align: center;
        margin: 0px 10px;
        font-size: 16px;
        border: none;
        background-color: transparent;
        &:focus {
          outline: 0; } } } } }

.editRoomNum {
  position: fixed;
  transform: translateY(100%);
  width: 100%;
  transition: 0.5s;
  bottom: 0;
  padding: 27px 0px;
  background-color: rgba(black,0.9);
  border-radius: 8px 8px 0px 0px;
  &.active {
    transform: translateY(0%); }
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include phone {
      flex-wrap: wrap; }
    .number {
      @include phone {
        margin-bottom: 10px; } } }
  .text {
    font-size: 12px;
    color: grey; }
  .price {
    font-size: 24px;
    font-family: Raleway;
    color: #fff;
    font-weight: bold; }
  .btns {
    display: flex;
    @include phone {
      width: 100%; } }
  .cancelBtn,.reserveBtn {
    padding: 15px 32px;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: 0.5s;
    @include phone {
      width: 50%;
      text-align: center; } }
  .cancelBtn {
    border: 1px solid #fff;
    margin-right: 8px;
    &:hover {
      background-color: darken(red,15%);
      border: 1px solid darken(red,15%); } }
  .reserveBtn {
    display: block;
    background-color: $primaryColor;
    &:hover {
      background-color: darken($primaryColor,5%); } } }

.phoneSearchBar {
  display: none;
  padding: 15px;
  background-color: $lightPrimary;
  align-items: center;
  @include phone {
    display: flex; }
  & a {
    color: $darkenColor; }
  .text {
    margin-left: 20px;
    font-size: 12px; } }

.phoneHotelImgs {
  display: none;
  margin-bottom: 16px;
  @include phone {
    display: block; }
  & img {
    width: 100%;
    height: 275px;
    object-fit: cover; }
  .swiper-button-next {
    background-color: $lightPrimary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50%;
    &::after {
      font-size: 14px;
      color: #000;
      font-weight: bold; } }
  .swiper-button-prev {
    position: absolute;
    top: 50%;
    background-color: $lightPrimary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    &::after {
      font-size: 14px;
      color: #000;
      font-weight: bold; } }
  .swiper-button-prev.swiper-button-disabled {
    opacity: 0; }
  .swiper-button-next.swiper-button-disabled {
    opacity: 0; } }
.phoneSelectRoom {
  display: none;
  @include phone {
    display: block; }
  & > li {
    border: 1px solid #eee;
    border-radius: 4px;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  .infoDetail {
    display: flex;
    .img {
      width: 150px;
      height: 134px;
      object-fit: cover;
      border-radius: 4px 0px 0px 0px;
      @include small {
        height: 158px; } }
    .infoContent {
      padding: 10px;
      width: calc(100% - 150px); }
    .name {
      color: $darkenColor;
      font-weight: bold; }
    .bedNum {
      font-size: 12px; } }
  .phoneIcon {
    display: flex;
    flex-wrap: wrap;
    & li {
      width: calc(100% / 6);
      @include xxs {
        width: calc(100% / 4); } } }
  .person {
    & i {
      color: $darkenColor;
      letter-spacing: 3px; } }
  .roomNumContent {
    padding: 8px;
    background-color: $lightPrimary;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .roomNum {
      width: 150px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .min,.add {
        font-size: 28px;
        font-weight: bold;
        color: $darkenColor;
        cursor: pointer;
        width: 25px;
        text-align: center;
        user-select: none; }
      .input {
        max-width: 40px;
        text-align: center;
        margin: 0px 10px;
        font-size: 16px;
        border: none;
        background-color: transparent;
        &:focus {
          outline: 0; } } }
    .originPrice {
      font-size: 14px;
      text-decoration: line-through;
      text-align: right; }
    .price {
      color: $darkenColor;
      font-weight: bold;
      & span.active {
        color: $primaryColor; } } } }
