.footer.reserveFooter {
  max-width: 740px;
  display: flex;
  justify-content: space-between;
  @include pad {
    max-width: 690px;
    padding: 24px 0px; }
  @include phone {
    max-width: 390px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center; }
  .footerMenu {
    @include phone {
      margin-bottom: 15px; }
    & li {
      &:last-child {
        margin-right: 0; } } }
  .copyRight {} }
