
.member {
  margin-top: 24px;
  display: flex;
  padding-bottom: 134px;
  border-bottom: 1px solid #eee;
  @include pad {
    flex-wrap: wrap;
    padding-bottom: 0; }
  .bookings {
    width: 65%;
    margin-right: 46px;
    @include pad {
      width: 100%;
      margin-right: 0;
      order: 2; }
    .title {
      margin-bottom: 24px; }
    .status {
      margin-bottom: 24px;
      & a {
        font-weight: bold;
        color: $darkenColor;
        &:first-child {
          margin-right: 24px; }
        &:hover {
          color: $primaryColor; } }
      & a.active {
        color: $primaryColor; } } } }
.bookingList {
  @include phone {
    padding-bottom: 48px; }
  & > li {
    height: 216px;
    @include padPlus {
      height: 230px; }
    @include phone {
      flex-wrap: wrap;
      height: auto; } }
  & li {
    display: flex;
    border-bottom: 1px solid #eee;
    margin-bottom: 24px;
    padding-bottom: 24px;
    @include phone {
      border-bottom: none;
      padding-bottom: 0; }
    &:hover {
      & img {
        transform: scale(1.1); }
      .bookingContent {
        background-color: $lightPrimary; } }
    &:last-child {
      margin-bottom: 0;
      border-bottom: none; } }
  .img {
    overflow: hidden;
    border-radius: 8px 0px 0px 8px;
    width: 35%;
    @include phone {
      width: 100%;
      border-radius: 8px 8px 0px 0px; }
    & img {
      border-radius: 8px 0px 0px 8px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center center;
      transform: scale(1.01);
      transition: 0.5s;
      @include phone {
        border-radius: 8px 8px 0px 0px; } } } }
.bookingContent {
  padding: 15px;
  width: 65%;
  display: flex;
  justify-content: space-between;
  transition: 0.5s;
  @include phone {
    flex-wrap: wrap;
    width: 100%;
    background-color: $lightPrimary;
    border-radius: 0px 0px 8px 8px;
    padding: 24px; }

  .content {
    color: $darkenColor;
    width: 70%;
    @include phone {
      width: 100%;
      margin-bottom: 24px; }
    & li {
      margin-bottom: 9px;
      padding-bottom: 0;
      border-bottom: none;
      &:last-child {
        margin-bottom: 0; } } }
  .icon {
    text-align: center;
    margin-right: 10px;
    width: 25px;
    @include phone {
      font-size: 18px; } }
  .name {
    font-weight: bold;
    color: $darkenColor; }
  .text {
    font-size: 14px;
    color: $secondaryColor; }
  .edit {
    font-size: 14px;
    color: $primaryColor;
    font-weight: bold;
    cursor: pointer;
    align-self: start;
    &:hover {
      color: darken($primaryColor,10%); } } }

.profile {
  width: 35%;
  @include pad {
    width: 100%;
    order: 1;
    margin-bottom: 48px; }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
    .text {
      display: flex;
      align-items: center; }
    .title {
      margin-right: 15px; }
    .verified {
      color: #fff;
      font-size: 12px;
      display: block;
      background-color: $primaryColor;
      border-radius: 99rem;
      display: flex;
      align-items: center;
      padding: 5px 10px;
      letter-spacing: 1px;
      & i {
        margin-right: 5px; } }
    .edit {
      font-size: 14px;
      color: $primaryColor;
      font-weight: bold;
      cursor: pointer;
      &:hover {
        color: darken($primaryColor,10%); } } }
  .detail {
    background-color: $lightPrimary;
    padding: 24px;
    border-radius: 8px;
    @include pad {
      display: flex; }
    @include phone {
      flex-wrap: wrap;
      padding: 15px; } }
  .userDetail {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    @include pad {
      width: 50%; }
    @include phone {
      width: 100%; }
    .title {
      font-weight: bold;
      color: $darkenColor;
      margin-bottom: 24px;
      @include pad {
        width: 100%; } }
    .text {
      margin-bottom: 16px;
      font-size: 14px;
      @include pad {
        display: none; }
      @include phone {
        display: block; }
      &:last-child {
        margin-bottom: 0; } }
    .photo {
      width: 64px;
      height: 64px;
      border-radius: 8px;
      overflow: hidden;
      @include pad {
        display: none; }
      @include phone {
        display: block; }
      & img {
        width: 100%; } }
    .photoPad {
      display: none;
      @include pad {
        display: flex; }
      @include phone {
        display: none; }
      .img {
        width: 64px;
        height: 64px;
        border-radius: 8px;
        overflow: hidden;
        margin-right: 16px;
        & img {
          width: 100%; } }
      .text {
        @include pad {
          display: block; } } } }

  .payment {
    @include pad {
      width: 50%; }
    @include phone {
      width: 100%; }
    .title {
      font-weight: bold;
      color: $darkenColor;
      margin-bottom: 24px; }
    .content {
      display: flex;
      & li {
        width: calc(100% / 2 );
        margin-right: 8px;
        padding: 16px;
        background-color: #fff;
        border-radius: 4px;
        font-size: 14px;
        position: relative;
        &:last-child {
          margin-right: 0; } } }
    .visa {
      color: $darkenColor;
      position: absolute;
      right: 9%;
      font-size: 18px;
      bottom: 15%;
      @include padPlus {
        bottom: 11%; }
      @include phone {
        bottom: 16%; } }
    .add {
      background-color: $primaryColor;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &:hover {
        background-color: darken($primaryColor,5%); } } } }
