.reservation {
  margin-top: 24px;
  display: flex;
  max-width: 740px;
  min-height: calc(100vh - 181px);
  border-bottom: 1px solid #eee;
  @include pad {
    max-width: 690px; }
  @include phone {
    max-width: 390px;
    flex-wrap: wrap;
    margin-top: 0; }
  @include small {
    max-width: 350px; }
  @include xxs {
    max-width: 300px; }
  .fillInInfo {
    width: 50%;
    margin-right: 30px;
    @include phone {
      width: 100%;
      margin-right: 0;
      margin-top: 80px; } }
  .title {
    margin-bottom: 24px;
    @include phone {
      font-size: 24px; } }
  .form {
    margin-bottom: 5px;
    & label,& input {
      display: block; }
    & label {
      margin-bottom: 5px; }
    & input {
      width: 100%;
      border-radius: 4px;
      border: 1px solid $lightColor;
      padding: 16px 10px;
      font-size: 16px;
      margin-bottom: 16px;
      font-family: "Open Sans";
      &:focus {
        outline: 0;
        background-color: $lightPrimary;
        border: 1px solid $lightPrimary; } }
    .reserveBtn {
      border: none;
      border-radius: 4px;
      background-color: $primaryColor;
      color: #fff;
      font-weight: bold;
      transition: 0.5s;
      display: block;
      padding: 16px 10px;
      text-align: center;
      margin-top: 20px;
      &:hover {
        background-color: darken($primaryColor,5%); } } }
  .remark {
    font-size: 14px;
    text-align: center;
    color: $primaryColor;
    @include phone {
      margin-bottom: 48px; } }

  .detail {
    width: 50%;
    border-radius: 8px;
    background-color: $lightPrimary;
    padding: 24px;
    align-self: flex-start;
    @include phone {
      display: none;
      width: 100%;
      border-radius: 0px 0px 8px 8px;
      padding: 15px;
      box-shadow: 0 10px 15px rgba(grey,0.4);
      position: absolute;
      width: 100%;
      left: 0;
      top: 113px; }
    & li {
      border-bottom: 1px solid #fff;
      margin-bottom: 24px;
      padding-bottom: 24px;
      &:last-child {
        border-bottom: 0;
        padding-bottom: 0;
        margin-bottom: 0; } }
    .title {
      margin-bottom: 16px;
      font-weight: bold;
      color: $darkenColor;
      @include phone {
        font-size: 18px;
        margin-bottom: 10px; } }
    .text {
      font-size: 14px;
      margin-bottom: 8px;
      &:last-child {
        margin-bottom: 0; } }
    .summary {
      font-size: 14px;
      & li {
        display: flex;
        border-bottom: 0;
        margin-bottom: 8px;
        padding-bottom: 0;
        justify-content: space-between; } }
    .totlaPrice {
      display: flex;
      justify-content: space-between;
      .price {
        font-weight: bold;
        color: $darkenColor; } } } }

.swal2-toast-shown .swal2-shown .swal2-timer-progress-bar {
  background-color: #b3e6bb; }
.phoneOrderTotal {
  display: none;
  position: absolute;
  left: 0;
  width: 100%;
  padding: 15px;
  align-items: center;
  justify-content: space-between;
  background-color: $lightPrimary;
  @include phone {
    display: flex; }
  .num {
    color: $darkenColor;
    font-weight: bold;
    margin-left: 5px; }
  .total {
    transition: 0.5s;
    &.active {
      opacity: 0; } }
  .phoneDetail {
    cursor: pointer;
    color: $primaryColor;
    font-weight: bold;
    .detailText {
      margin-right: 10px; }
    .arrow {
      transition: 0.5s;
      &.active {
        transform: rotate(180deg); } } } }
