.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 0px;
  @include phone {
    padding: 15px 0px; } }
.logo {
  @include phone {
    display: none; }
  & a {
    background-image: url(../img/logo.svg);
    width: 100px;
    height: 25px;
    display: block;
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap; } }
.menu {
  display: flex;
  align-items: center;
  @include phone {
    display: none; }
  .lang {
    display: flex;
    align-items: center;
    margin-top: 3px;
    & i {
      margin-right: 8px;
      color: $darkenColor; }
    & select {
      padding: 2px;
      border: none;
      font-size: 16px;
      cursor: pointer;
      font-family: "Open Sans";
      font-weight: 500;
      &:focus {
        outline: 0; } } }
  & li {
    margin-right: 48px;
    & a {
      color: $darkenColor;
      font-family: Roboto;
      font-weight: 600;
      &:hover {
        color: $primaryColor; } }
    &:last-child {
      margin-right: 0; } } }
.phoneMenu {
  display: none;
  @include phone {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between; }
  .phoneIcon {
    width: 30px;
    cursor: pointer;
    font-size: 18px; }
  .phoneLogo {
    & a {
      background-image: url(../img/logo.svg);
      background-size: cover;
      width: 100px;
      height: 26px;
      display: block;
      text-indent: 101%;
      overflow: hidden;
      white-space: nowrap; } }
  .phoneLogin {
    & a {
      color: $darkenColor;
      font-family: Roboto;
      font-weight: 600; } } }

.phoneDropDown {
  display: none;
  position: absolute;
  width: 100%;
  left: 0;
  top: 56px;
  z-index: 99;
  background-color: $lightPrimary;
  & li {
    padding: 10px;
    border-bottom: 1px solid #fff;
    &:last-child {
      border-bottom: none; }
    &:active {
      background-color: darken($lightPrimary,5%); } }
  & a {
    display: block;
    color: $primaryColor;
    font-family: Roboto;
    font-weight: 600; }
  & i {
    margin-right: 10px; }
  .phoneSelect {
    border: none;
    font-size: 16px;
    font-family: "Open Sans";
    font-weight: 500;
    &:focus {
      outline: 0; } } }
