.searchBar {
  padding: 24px 0px;
  background-color: $lightPrimary;
  margin-bottom: 24px;
  @include padPlus {
    padding: 16px 0px; }
  @include phone {
    display: none; } }
.searchBarContent {
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
  .search {
    display: flex;
    align-items: center;
    width: 87%;
    @include padPlus {
      width: 100%; }
    & li {
      display: flex;
      align-items: center;
      background-color: #fff;
      padding: 10px 16px;
      margin-right: 16px;
      border-radius: 4px;
      width: calc(100% / 3);
      @include pad {
        margin-right: 8px;
        padding: 10px 8px; }
      &:nth-child(2) {
        @include md {
          width: 34%; }
        @include padPlus {
          width: 40%; } }
      &:nth-child(3) {
        @include md {
          width: 27%; }
        @include padPlus {
          width: 35%; } }
      &:last-child {
        margin-right: 0;
        @include padPlus {
          margin-right: 16px; }
        @include pad {
          margin-right: 8px; } }
      .icon {
        margin-right: 16px;
        & i {
          font-size: 20px;
          color: $darkenColor; } }
      .name {
        color: $darkenColor;
        font-weight: bold;
        font-size: 12px; }
      .country,.time {
        font-size: 14px; }
      .checkTime {
        display: flex;
        align-items: center;
        width: 100%; }
      .checkIn {
        margin-right: 20px;
        border-right: 1px solid #eee;
        padding-right: 50px;
        @include pad {
          margin-right: 10px;
          padding-right: 10px; } } } }
  .searchBtn {
    border: 1px solid;
    display: block;
    padding: 16px 30px;
    border-radius: 4px;
    background-color: $primaryColor;
    transition: 0.5s;
    color: #fff;
    font-weight: bold;
    &:hover {
      background-color: darken($primaryColor,5%); } } }

.searchContent {
  display: flex;
  margin-bottom: 72px;
  @include phone {
    flex-wrap: wrap; }
  .filter {
    width: 30%;
    margin-right: 30px;
    @include phone {
      width: 100%;
      margin-right: 0; }
    .subTitle {
      margin-bottom: 24px;
      @include phone {
        display: none; }
      & sup {
        font-size: 20px;
        margin-left: 5px;
        color: $primaryColor; } } }
  .filterItem {
    background-color: $lightPrimary;
    border-radius: 8px;
    padding: 24px;
    &.active {
      transform: translateY(0%); }
    @include phone {
      position: absolute;
      top: 0;
      width: 100%;
      left: 0;
      z-index: 99;
      transform: translateY(-100%);
      transition: 0.5s; }
    & li {
      border-bottom: 1px solid #fff;
      padding-bottom: 15px;
      margin-bottom: 20px;
      &:last-child {
        border-bottom: none;
        padding-bottom: 0;
        margin-bottom: 0; } }
    .name {
      color: $darkenColor;
      font-weight: bold;
      margin-bottom: 16px; }
    .price {
      display: flex;
      align-items: center;
      & span {
        margin: 0px 10px;
        @include padPlus {
          margin: 0 5px; }
        @include pad {
          display: none; }
        @include phone {
          display: block; } } }
    .minPrice,.maxPrice {
      background-color: #fff;
      padding: 5px 10px;
      width: 50%;
      border-radius: 4px;
      @include pad {
        padding: 5px; }
      @include phone {
        padding: 10px; }
      .text {
        font-weight: bold;
        font-size: 12px;
        color: $darkenColor; }
      .num {
        font-size: 14px;
        @include pad {
          font-size: 12px; } } }
    .minPrice {
      @include pad {
        border-radius: 4px 0px 0px 4px;
        border-right: 1px solid #eee; }
      @include phone {
        border-radius: 4px; } }
    .maxPrice {
      @include pad {
        border-radius: 0px 4px 4px 0px; }
      @include phone {
        border-radius: 4px; } }

    .slider {
      margin-bottom: 23px;
      height: 2px;
      .noUi-connects {
        background-color: #d8d3f8;
        height: 2px; }
      .noUi-connect {
        background-color: $primaryColor; }
      .noUi-origin {
        .noUi-handle {
          border: 1px solid transparent; }
        .noUi-handle.noUi-handle-lower,.noUi-handle.noUi-handle-upper {
          width: 15px;
          height: 15px;
          right: -15px;
          top: -6px;
          border-radius: 50%;
          background: $primaryColor;
          box-shadow: none;
          cursor: pointer; }
        .noUi-handle::before {
          display: none; }
        .noUi-handle::after {
          display: none; } } }

    .checkbox {
      position: relative;
      padding-left: 35px;
      margin-bottom: 10px;
      cursor: pointer;
      user-select: none;
      @include pad {
        padding-left: 30px; }
      & label {
        cursor: pointer;
        @include pad {
          font-size: 14px; } } }
    .checkbox {
      & input {
        position: absolute;
        opacity: 0;
        height: 0;
        width: 0;
        cursor: pointer; } }
    .checkmark {
      position: absolute;
      top: 3px;
      left: 0;
      height: 20px;
      width: 20px;
      border: 2px solid $darkenColor; }
    .checkbox input:checked ~ .checkmark {
      background-color: $primaryColor;
      border: none; }
    .checkbox .checkmark::before {
      left: 8px;
      top: 4px;
      width: 5px;
      height: 10px;
      border: solid #fff;
      border-width: 0px 3px 3px 0;
      transform: rotate(45deg); }
    .checkmark::before {
      content: "";
      position: absolute;
      display: none; }
    .checkbox input:checked ~ .checkmark::before {
      display: block; }
    .rating {
      & i {
        color: $darkenColor;
        margin-right: 2px; }
      .num {
        margin-left: 10px; } }
    .phoneFilter {
      display: none;
      align-items: center;
      justify-content: space-between;
      font-weight: bold;
      color: $darkenColor;
      background-color: #fff;
      padding-bottom: 0;
      margin-right: -24px;
      margin-left: -24px;
      margin-top: -24px;
      padding: 20px 24px;
      @include phone {
        display: flex; }
      .filterTitle {
        display: flex;
        align-items: center;
        & i {
          transform: rotate(180deg);
          width: 20px;
          margin-right: 10px;
          cursor: pointer; } }
      & i {
        width: 20px; }
      .delIcon {
        cursor: pointer; } } }

  .filterResult {
    padding-top: 15px;
    width: 70%;
    @include pad {
      width: 66%; }
    @include phone {
      width: 100%; }
    .sort {
      display: flex;
      margin-bottom: 24px;
      @include pad {
        display: none; }
      @include phone {
        position: absolute;
        top: 220px;
        width: 90%;
        right: 5%;
        border-radius: 8px;
        padding: 0 15px;
        background-color: $lightPrimary;
        box-shadow: 1px 5px 10px rgba(grey,0.3);
        margin-bottom: 0;
        overflow: hidden;
        z-index: 99;
        &.active {
          display: block; } }

      & li {
        margin-right: 24px;
        color: $darkenColor;
        font-weight: bold;
        cursor: pointer;
        transition: 0.5s;
        @include phone {
          padding: 10px 20px;
          margin-right: -15px;
          margin-left: -15px; }
        &:hover,&.active {
          color: $primaryColor; }
        &:last-child {
          margin-right: 0; } } }
    .swiper {
      margin-bottom: 16px;
      display: none;
      @include pad {
        display: block; }
      @include phone {
        display: none; } }
    .swiper-slide {
      text-align: center;
      background: #fff;
      color: $darkenColor;
      font-weight: bold;
      line-height: 20px;
      &.active {
        color: $primaryColor; } }
    .sortResult {
      margin-bottom: 48px;
      & li a {
        display: flex;
        padding-bottom: 25px;
        border-bottom: 1px solid #eee;
        margin-bottom: 24px;
        height: 200px;
        @include padPlus {
          height: 220px; }
        @include phone {
          padding-bottom: 0;
          margin-bottom: 0;
          border-bottom: 4px solid #eee;
          height: 170px; }
        @include xxs {
          flex-wrap: wrap;
          height: auto;
          border-bottom: 1px solid #eee;
          margin-bottom: 10px; }
        &:hover {
          .content {
            background-color: $lightPrimary; }
          & img {
            transform: scale(1.1); }
          .tags span {
            border: 1px solid $primaryColor;
            color: $primaryColor; } } }
      .img {
        width: 30%;
        border-radius: 8px 0px 0px 8px;
        overflow: hidden;
        @include pad {
          width: 40%; }
        @include phone {
          width: 60%; }
        @include xxs {
          width: 100%;
          border-radius: 8px; }
        & img {
          border-radius: 8px 0px 0px 8px;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center center;
          transition: 0.5s;
          transform: scale(1.01);
          @include xxs {
            border-radius: 8px;
            height: 200px; } } }
      .content {
        width: 70%;
        padding: 16px;
        color: $secondaryColor;
        transition: 0.5s;
        @include pad {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding: 8px 10px; }
        @include phone {
          padding: 10px; }
        @include xxs {
          width: 100%; }
        & h3 {
          @include pad {
            font-size: 16px; } }
        .title {
          display: flex;
          justify-content: space-between;
          margin-bottom: 16px;
          @include pad {
            margin-bottom: 0px; }
          @include phone {
            flex-wrap: wrap; } }
        .name {
          @include phone {
            width: 100%; } }
        .distance {
          color: $primaryColor;
          font-size: 14px;
          @include pad {
            font-size: 12px; }
          & span {
            color: $secondaryColor;
            @include pad {
              display: none; } } }
        .star {
          @include phone {
            display: flex; }
          & i {
            color: $darkenColor;
            letter-spacing: 1px; }
          & span {
            margin-left: 10px;
            font-size: 14px;
            @include pad {
              display: block;
              text-align: right;
              font-size: 12px; }
            @include phone {
              text-align: left;
              margin-left: 10px; } } }
        .info {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          @include pad {
            flex-wrap: wrap; }
          .infoContent {
            align-self: flex-start;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @include phone {
              margin-bottom: 20px; } }
          .type {
            font-weight: bold;
            font-size: 14px; }
          .text {
            font-size: 12px;
            margin-bottom: 10px;
            @include phone {
              margin-bottom: 0; } } }
        .tags {
          @include phone {
            display: none; }
          & span {
            font-size: 12px;
            margin-right: 10px;
            padding: 5px 8px;
            background-color: $lightPrimary;
            border-radius: 99rem;
            border: 1px solid $lightPrimary;
            transition: 0.5s;
            @include pad {
              display: inline-block;
              margin-bottom: 4px; }
            &:last-child {
              @include pad {
                margin-bottom: 0; } } } }
        .salePrice {
          text-align: right;
          @include pad {
            width: 100%; }
          @include phone {
            display: none; }
          .night {
            font-size: 12px;
            text-align: right;
            @include phone {
              display: none; } }
          & span {
            font-family: Raleway;
            &.active {
              color: $primaryColor; } }
          .origin {
            font-size: 16px;
            text-align: right;
            color: $secondaryColor;
            font-weight: 200;
            text-decoration: line-through;
            @include pad {
              font-size: 14px; } } }
        .phoneSalePrice {
          display: none;
          @include phone {
            display: block; } } } }
    .pagination {
      background-color: $lightPrimary;
      padding: 16px;
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include xxs {
        padding: 16px 10px; } }
    .previous {
      border: 1px solid $primaryColor;
      padding: 5px 15px;
      border-radius: 4px;
      color: $primaryColor;
      font-weight: bold;
      transition: 0.5s;
      cursor: pointer;
      @include pad {
        display: none; }
      &:hover {
        color: #fff;
        background-color: $primaryColor; } }
    .previous.pad {
      display: none;
      &:hover {
        color: #fff;
        background-color: $primaryColor; }
      @include pad {
        display: block; }
      @include xxs {
        padding: 5px 10px; } }
    .next {
      border: 1px solid $primaryColor;
      color: $primaryColor;
      padding: 5px 15px;
      border-radius: 4px;
      font-weight: bold;
      transition: 0.5s;
      cursor: pointer;
      @include pad {
        display: none; }
      &:hover {
        color: #fff;
        background-color: $primaryColor; } }
    .next.pad {
      display: none;
      @include pad {
        display: block; }
      @include xxs {
        padding: 5px 10px; } }
    .numbers {
      display: flex;
      & li {
        margin-right: 10px;
        transition: 0.5s;
        &:hover {
          background-color: #fff; }
        &:last-child {
          margin-right: 0; }
        & a {
          color: $darkenColor;
          font-weight: bold;
          display: block;
          padding: 10px 14px;
          @include pad {
            padding: 6px 14px; }
          @include small {
            padding: 6px 13px; }
          @include xxs {
            padding: 6px 10px; } }
        &.active {
          background-color: #fff; }
        &:nth-child(4) {
          @include pad {
            display: none; } }
        &:nth-child(5) {
          @include pad {
            display: none; } }
        &:nth-child(6) {
          @include pad {
            display: none; } } } } } }

.footerSubscribe {
  padding: 34px 0;
  background-color: $lightPrimary;
  @include padPlus {
    padding: 24px 0px; }
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include phone {
      flex-wrap: wrap; } }
  .text {
    width: 40%;
    @include phone {
      width: 100%;
      font-size: 24px;
      margin-bottom: 16px;
      text-align: center; } }
  .subscribeInput {
    display: flex;
    align-items: center;
    width: 56%;
    @include phone {
      width: 100%; }
    .input {
      border: none;
      padding: 16px;
      font-family: "Open Sans";
      font-size: 16px;
      border-radius: 4px 0px 0px 4px;
      width: 100%;
      border: 1px solid #fff;
      @include xxs {
        padding: 16px 14px; }
      &:focus {
        outline: 0;
        border: 1px solid $primaryColor; } }
    .btn {
      background-color: $primaryColor;
      border: none;
      color: #fff;
      padding: 19px 20px;
      border-radius: 0px 4px 4px 0px;
      font-weight: bold;
      cursor: pointer;
      font-size: 16px;
      transition: 0.5s;
      &:hover {
        background-color: darken($primaryColor,5%); } } } }

.phoneContent {
  display: none;
  @include phone {
    display: block;
    width: 100%; }
  .searchResult {
    display: flex;
    background-color: $lightPrimary;
    padding: 8px 0px;
    margin-bottom: 16px;
    & li {
      padding: 10px;
      width: calc(100% / 3);
      text-align: center;
      font-size: 12px;
      border-right: 1px solid #fff;
      @include xxs {
        padding: 5px 0px; } } }
  .title {
    text-align: center;
    margin-bottom: 10px;
    & sup {
      color: $primaryColor;
      font-size: 16px; } }
  .select {
    display: flex;
    cursor: pointer;
    & li {
      width: 50%;
      display: flex;
      align-items: center;
      padding: 10px;
      justify-content: center;
      color: $darkenColor;
      font-weight: bold;
      border-right: 1px solid #eee;
      &:active {
        color: $primaryColor; } }
    & i {
      margin-right: 10px;
      transform: rotate(180deg); } }
  .flterResult {
    padding: 27px 15px;
    background-color: rgba(black,0.9);
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 100;
    border-radius: 8px 8px 0px 0px;
    transform: translateY(100%);
    transition: 0.5s;
    &.active {
      transform: translateY(0%); }
    .text {
      color: #fff;
      font-size: 25px;
      font-family: Raleway;
      font-weight: 600;
      margin-bottom: 10px; }
    .btns {
      display: flex;
      font-weight: bold; }
    .clearBtn,.filterBtn {
      width: 50%;
      padding: 15px;
      text-align: center;
      border-radius: 4px;
      color: #fff;
      cursor: pointer; }
    .clearBtn {
      border: 1px solid #fff;
      margin-right: 10px;
      transition: 0.5s;
      &:hover {
        background-color: #b30000; }
      &:active {
        background-color: #b30000; } }
    .filterBtn {
      background-color: $primaryColor;
      transition: 0.5s;
      &:hover {
        background-color: darken($primaryColor,5%); }
      &:active {
        background-color: darken($primaryColor,5%); } } } }
